/* 정진초 고시반 웹사이트 CSS */
:root {
  --crimson: #730220;
  --light-crimson: #E6496A;
  --gray: #f5f5f5;
}

.jincho-website {
  margin: 0;
  font-family: 'Noto Sans KR', sans-serif;
  background: var(--gray);
}

.header {
  display: flex;
  align-items: center;
  padding: 16px 32px;
  background: #fff;
  box-shadow: 0 2px 4px #0001;
}

.header img {
  height: 48px;
  margin-right: 16px;
}

.header-title {
  color: var(--crimson);
  font-size: 1.4rem;
  font-weight: bold;
  flex: 1;
  line-height: 1.2;
  text-align: left;
}

.header-title .first-line {
  font-size: 1.2rem;
}

.header-title .second-line {
  font-size: 1.4rem;
}

.nav {
  display: flex;
  gap: 32px;
}

.nav a {
  color: #222;
  text-decoration: none;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
}

.main-visual {
  background: var(--crimson);
  color: #fff;
  text-align: center;
  padding: 48px 16px 48px 16px;
}

.main-visual h1 {
  font-size: 2.5rem;
  margin-bottom: 26px;
  margin-top: 0;
}

.main-visual p {
  font-size: 1.25rem;
  opacity: 0.95;
  margin-bottom: 32px;
  margin-top: 0;
}

.cta-btn {
  background: #fff;
  color: var(--crimson);
  border: none;
  border-radius: 16px;
  padding: 18px 40px;
  font-size: 1.1rem;
  font-weight: bold;
  margin-top: 20px;
  cursor: pointer;
  transition: all 0.2s;
  box-shadow: 0 4px 12px #0002;
  margin-right: 16px;
  width: 210px;
}

.cta-btn:hover {
  background: var(--light-crimson);
  color: #fff;
}

.section {
  max-width: 700px;
  margin: 40px auto;
  background: #fff;
  border-radius: 16px;
  box-shadow: 0 2px 8px #0001;
  padding: 32px 24px;
}

.section-title {
  color: var(--crimson);
  font-size: 1.6rem;
  margin-bottom: 12px;
  font-weight: bold;
  text-align: left;
}

.notice-card {
  background: var(--gray);
  border-left: 6px solid var(--crimson);
  margin-bottom: 16px;
  padding: 20px 20px 18px 32px;
  border-radius: 12px;
  text-align: left;
}

.footer {
  margin-top: 40px;
  background: var(--crimson);
  color: #fff;
  padding: 28px 0;
  text-align: left;
  font-size: 1rem;
  padding-left: 32px;
}

.schedule-card {
  background: var(--gray);
  border-left: 6px solid #ccc;
  margin-bottom: 16px;
  padding: 20px 20px 18px 32px;
  border-radius: 12px;
  transition: all 0.3s ease;
  text-align: left;
}

.schedule-card:hover {
  border-left-color: var(--crimson);
  background: #f0f0f0;
}

.crimson-card {
  background: var(--gray);
  border-left: 6px solid var(--crimson);
  margin-bottom: 16px;
  padding: 20px 20px 18px 32px;
  border-radius: 12px;
  font-size: 1rem;
  text-align: left;
}

.schedule-notice {
  font-size: 0.9rem;
  color: #666;
  margin-bottom: 20px;
  text-align: left;
}

.section p {
  text-align: left;
}

.section ul {
  text-align: left;
}

.section li {
  text-align: left;
}

.copy-email {
  cursor: pointer;
  color: var(--crimson);
  text-decoration: underline;
}

.copy-email:hover {
  color: var(--light-crimson);
}

.floating-notice {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
}

.notice-popup {
  position: relative;
  background: #fff;
  border-radius: 16px;
  padding: 32px;
  max-width: 500px;
  width: 90%;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

.notice-popup h3 {
  color: var(--crimson);
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 1.4rem;
  text-align: left;
}

.notice-popup p {
  margin-bottom: 24px;
  line-height: 1.5;
  text-align: left;
}

.notice-popup .buttons {
  display: flex;
  gap: 12px;
  justify-content: flex-end;
}

.notice-btn {
  padding: 12px 20px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
  transition: all 0.2s;
}

.notice-btn.primary {
  background: var(--crimson);
  color: #fff;
}

.notice-btn.primary:hover {
  background: var(--light-crimson);
}

.notice-btn.secondary {
  background: #f0f0f0;
  color: #333;
}

.notice-btn.secondary:hover {
  background: #e0e0e0;
}

.detail-popup {
  position: relative;
  background: #fff;
  border-radius: 16px;
  padding: 32px;
  max-width: 600px;
  width: 90%;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  max-height: 80vh;
  overflow-y: auto;
}

.detail-popup h3 {
  color: var(--crimson);
  margin-top: 0;
  margin-bottom: 20px;
  font-size: 1.5rem;
  border-bottom: 2px solid var(--crimson);
  padding-bottom: 10px;
}

.detail-popup .content {
  line-height: 1.6;
  margin-bottom: 24px;
  white-space: pre-line;
  text-align: left;
}

.detail-popup .close-btn {
  float: right;
  background: var(--crimson);
  color: #fff;
  border: none;
  padding: 12px 24px;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
}

.detail-popup .close-btn:hover {
  background: var(--light-crimson);
}

.clickable-card {
  cursor: pointer;
  transition: all 0.3s ease;
}

.clickable-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
}

/* 반응형 디자인 */
@media (max-width: 768px) {
  .header {
    flex-direction: column;
    gap: 16px;
    padding: 16px;
  }

  .nav {
    gap: 16px;
  }

  .main-visual {
    padding: 32px 16px;
  }

  .main-visual h1 {
    font-size: 2rem;
  }

  .cta-btn {
    width: 100%;
    margin-right: 0;
    margin-bottom: 12px;
  }

  .section {
    margin: 20px 16px;
    padding: 24px 16px;
  }

  .notice-popup,
  .detail-popup {
    margin: 16px;
    width: calc(100% - 32px);
  }
}

@media (max-width: 480px) {
  .header-title {
    font-size: 1.2rem;
  }

  .header-title .first-line {
    font-size: 1rem;
  }

  .header-title .second-line {
    font-size: 1.2rem;
  }

  .main-visual h1 {
    font-size: 1.5rem;
  }

  .main-visual p {
    font-size: 1rem;
  }
} 